import React, { useState } from 'react';
import Modal from '../modal/modal';
import { supabase } from '../../supabaseClient';
import apple from '../../resources/svg/Apple.svg';
import google from '../../resources/svg/Google.svg';
import facebook from '../../resources/svg/Facebook.svg';
import '../modal/modal.css';

const SignupBtn = () => {
  const [modalActive, setModalActive] = useState(false);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSignup = async () => {
    setLoading(true);
    const { error } = await supabase.auth.signUp({
      email,
      password
    });

    if (error) {
      alert('Eroare la nregistrare: ' + error.message);
    } else {
      alert('Cont creat! Verific emailul pentru confirmare.');
      setModalActive(false);
      setEmail('');
      setPassword('');
    }

    setLoading(false);
  };

  return (
    <>
      <button id='login' className="btn-registr" onClick={() => setModalActive(true)}>
        registrare
      </button>
      <Modal id="login" active={modalActive} setActive={setModalActive}>
        <div className='signup-mod'>
          <div className="motiv_text">nscriei-v pentru a colecta art de la cei mai importani artiti din Moldova</div>
          <div className='container-signup'>

            <div className="signup_boxl">
              <div className="input_nume">
                <input
                  type="email"
                  placeholder="Nume de utilizator sau email"
                  required
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
              <div className="input_parola">
                <input
                  type="password"
                  placeholder="********"
                  required
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>
              <div className="subtext">Parola trebuie s aib cel puin 8 caractere i s includ o liter minuscul, liter mare i o cifr.</div>
              <div className="button_registra" onClick={handleSignup}>
                {loading ? 'Se nregistreaz...' : 'nregistreaz'}
              </div>
            </div>

            <div className='signup_boxr'>
              <div className="sau">sau</div>
              <div className="variante-signup">
                <div className="cuApple"> <img src={apple} alt="" /> Continu cu Apple</div>
                <div className="google"> <img src={google} alt="" /> Continu cu Google</div>
                <div className="facebook"> <img src={facebook} alt="" /> Continu cu Facebook</div>
              </div>
              <div className="link_login">Ai deja un cont? <a href="/login">Conectare</a></div>
            </div>

          </div>
          <div className="termen_de_util">Fcnd clic pe nscriei-v sau Continuai cu Apple, Google sau Facebook, suntei de acord cu Termenii de utilizare i Politica de confidenialitate a Artsy.</div>
        </div>
      </Modal>
    </>
  );
};

export default SignupBtn;